/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: #fff;
}

body {
  text-align: center;
  padding: 0 40px;
}

body header {
  height: 30px;
  -webkit-app-region: drag;
}

body main {
  padding-bottom: 20px;
}

body main figure {
  width: 128px;
  height: 128px;
  margin: 10px auto 20px;
}

body main figure img {
  display: block;
}

body main h1 {
  font-size: 14px;
  margin-bottom: 10px;
}

body main h1 #version {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 200;
  padding-left: 10px;
}

body main p {
  font-size: 11px;
  font-weight: 200;
  color: rgba(255, 255, 255, 0.6);
}

body footer table {
  margin: 0 auto;
  font-size: 10px;
  line-height: 14px;
  font-weight: 200;
  color: rgba(255, 255, 255, 0.4);
}

body footer table th,
body footer table td {
  padding: 0 4px;
}

body footer table th {
  text-align: right;
}

body footer table td {
  text-align: left;
}

body footer table tfoot button {
  display: block;
  width: auto;
  height: 20px;
  line-height: 11px;
  margin: 10px auto 0;
  background: #031320;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 2px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  font-weight: 200;
  text-align: center;
  transition: all .15s ease-in-out;
}

body footer table tfoot button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

body footer table tfoot button:active {
  background-color: #020b12;
  box-shadow: inset 0 2px 4px #000204;
  outline: none;
}
